import './Contact.css';
import React from 'react';
import gmail from './images/gmail.png';
import linkedin from './images/linkedin.png';
import github from './images/github.png';
import twitter from './images/twitter.png';
import discord from './images/discord.png';

// import {Image} from 'react-native';
function Contact(){

    return(

        <div className="Contact">
            <div className='contane'>

            
            <div className="imge">
                    <a href="mailto: dan.smith.depaz@gmail.com" target="_blank">
                        <img src={gmail}  className='gmail images' />
                    </a>
            </div>
            <div className="imge">
                <a href="https://www.linkedin.com/in/dansdp/" target="_blank">
                    <img src={linkedin}  className='linkedin images' />
                </a>
            </div>
            <div className="imge">
                <a href="https://github.com/adeadzeplin" target="_blank">
                    <img src={github}  className='github images' />
                </a>
            </div>
            <div className="imge">
                <a href="https://twitter.com/adeadzeplin" target="_blank">
                    <img src={twitter}  className='twitter images' />
                </a>
            </div>
            <div className="imge">
                <a href="https://discord.com/users/302898426851491841" target="_blank">
                    <img src={discord}  className='discord images' />
                </a>
            </div>
            </div>

        </div>

    )
}
export default Contact;