import './About.css';
import React from 'react';
import munch from './images/munch.png';

function About(){

    return(

        <div className="About">
            <div className='Column1'>
                <div className='Me aboutbox'>
                    <img src={munch} alt="munch" className='munch' />
                    <div className="MyName">
                        <h1>I'm Dan</h1>
                        <p>Born and raised in the butt-crack of New Jersey.</p>
                        <p>I like dumb stuff.</p>
                    </div>
                </div>

                <div className="Degree aboutbox">
                    <div className="Schooling">
                        <h1>BS of Computer Engineering</h1>
                        <p>Gradutated from Wentworth institute of technology</p>
                        <p>Class of 2020</p>
                    </div>
                    {/* <img src={wit1} alt="wit1" className='school' /> */}
                </div>

                <div className="Skills aboutbox">
                    <div className="Egostroking">
                        <h1>Acquired Knowledge</h1>
                        <p>Proficient in C/C++ and Python. </p>
                        <p>Aquainted with: Circuit design, SQL, R, MATLAB, JavaScript, React, Verilog, LabVIEW, AutoDesk Inventor/Solidworks.</p>
                        <p>Certified: Mechanical drafter, HAM Radio Operator</p>
                    </div>
                    {/* <img src={circuitry} alt="circuitry" className='circuitry' /> */}
                </div>

            </div>
            
        </div>

    )
}
export default About;