import './Projects.css';
import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiAccordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import MuiAccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';

const Accordion = withStyles({
  root: {
    border: '1px solid white',
    boxShadow: 'none',
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
    '&$expanded': {
      margin: 'auto',
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: '#3c3c3c',
    color: 'white',
    borderBottom: '1px solid white',
    marginBottom: -1,
    minHeight: 56,
    '&$expanded': {
      minHeight: 56,
      backgroundColor: '#396CF0',
    },
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
      color: 'white',
    },
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    backgroundColor: '#3c3c3c',
    color: 'white',
  },
}))(MuiAccordionDetails);

export default function CustomizedAccordions() {
  const [expanded, setExpanded] = React.useState('panel1');

  const handleChange = (panel) => (event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  return (
    <div>
      <Accordion square expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
        <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
          <Typography><h1>Designing Circuits with Machine Learning</h1></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <h3>Senior Design Project</h3>
            <ul className='textdata'>
                <li>The goal of the project was to compare the viability of two machine Learning algorithms competency at creating gate-level circuits.</li>
                <li>My role in this project was to create and implement a Q-learning AI that utilizes Monte Carlo policy evaluation.</li>
                <a href="https://github.com/adeadzeplin/seniordesign" className='codelink' target="_blank">Code Repo</a>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary aria-controls="panel2d-content" id="panel2d-header">
          <Typography><h1>Random Robes</h1></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <h3>A mod for a steam game: Noita</h3>
          <p className='textdata'>I wrote a script in python to autonomously:</p>
            <ul  >
                <li> randomly generate color pallets </li>
                <li> while ensuring uniqueness </li>
                <li> create custom 2d character models</li>
                <li> update Lua code of the game files</li>       
              </ul>
                <p><a href="https://github.com/adeadzeplin/noitamods" className='codelink' target="_blank">Code Repo</a>         
                </p>
                <p>
                  <a href="https://steamcommunity.com/sharedfiles/filedetails/?id=1989654152" className='codelink' target="_blank">Steam Page</a>         
                </p>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
        <AccordionSummary aria-controls="panel3d-content" id="panel3d-header">
          <Typography><h1>Antikythera</h1></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <h3>Solar Simulation</h3>
          <p className='textdata'>
              I worked as a part of a group of 6 people to create an accurate celestial body
              application in Python. My team role was to create and manage a database that the application could search/interface with.
          </p>
          <a href="https://github.com/adeadzeplin/Kythera" className='codelink' target="_blank">Code Repo</a>         

          </Typography>
        </AccordionDetails>
      </Accordion>
     
      <Accordion square expanded={expanded === 'panel4'} onChange={handleChange('panel4')}>
        <AccordionSummary aria-controls="panel4d-content" id="panel4d-header">
          <Typography><h1>Discord Bot</h1></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <h3>An interactable Bot User</h3>
          <p className='textdata'>I created a bot in python to to heckle my friends with silly things:</p>
            <ul  >
                <li> Randomly generate insults. </li>
                <li> Runs playable text based console game in chat. </li>
                <li> Intermittently Joins voice chat and plays random sound files</li>
                <li> Awards superflous titles random people.</li> 
                <li> Allows users to challenge and play each other at card games in discord</li>     
                
          <a href="https://github.com/adeadzeplin/discordbot" className='codelink' target="_blank">Code Repo</a>         

            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion square expanded={expanded === 'panel5'} onChange={handleChange('panel5')}>
        <AccordionSummary aria-controls="panel5d-content" id="panel5d-header">
          <Typography><h1>This very site!</h1></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <div className='textdata'>
                <p>Built using React!</p>
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}

