import './WorkExperience.css';
import React from 'react';
// import munch from './images/munch.png';

function WorkExperience(){

    return(

        <div className="WorkExperience">
            
            <div className='workbox left'>
                {/* <img src={munch} alt="munch" className='munch' /> */}
                <div className="MSAtext">
                    <h1>Software Engineering Coop @ <a href="https://us.msasafety.com/" className='MSAlink' target="_blank">MSA Safety</a>.</h1>
                    <h3>At MSA I wrote code for devices that alert users of dangerous chemicals in the air.</h3>
                    <h3>Some other stuff I did there:</h3>
                    <ul className='textdata'>
                        <li>Create development debug tools to improve developer quality of life</li>
                        <li>Create a Menu User Interface on a microcontroller written in C. Interfacing i/o with an OSAL.</li>
                        <li>Worked closely with domestic and international team members in Germany. Colaborated with people from multiple diciplines.</li>
                        <li>Optimize packet write-transmissions to an LCD peripheral to increase performance.</li>
                    </ul>
                    
                </div>
            </div>

            <div className="workbox right">
                <div className="Hydroidtext">
                <h1>Electrical Engineering Intern @ <a href="https://www.hydroid.com/" className='Hydroidlink' target="_blank">Hydroid inc</a>. </h1>
                    <h3> At Hydroid I provided support for Electrical Engineers working on various aquatic unmanned vehichles. (Submarine Drones!)</h3>
                    <h3>Some other stuff I did there:</h3>
                    <ul className='textdata'>
                        <li>Reduce labor costs by assisting in the modularization of product development.</li>
                        <li>Peer review electrical schematics pending implementation.</li>
                        <li>Create new electrical schematics based on engineering change requests.</li>
                    </ul>
                </div>
                {/* <img src={wit1} alt="wit1" className='school' /> */}
            </div>
         
        </div>

    )
}
export default WorkExperience;